import { Component, OnInit } from '@angular/core';
import { ManagepeopleService } from './shared/managepeople.service';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { Router } from '@angular/router';

@Component({
  selector: 'app-manage-people',
  templateUrl: './manage-people.component.html',
  styleUrls: ['./manage-people.component.css']
})
export class ManagePeopleComponent implements OnInit {

  constructor(
    private managepeopleService: ManagepeopleService,
    public toastr: ToastsManager,
    private router: Router) { }
  filter: string;
  volunteers: Array<any>;

  ngOnInit() {
    this.managepeopleService.getVolunteers().subscribe(res => {
      this.volunteers = res;
      console.log(res);
    });

  }
  listEmployees() {
    document.getElementById('volun').classList.remove('psln-active');
    document.getElementById('fost').classList.remove('psln-active');
    document.getElementById('emplo').classList.add('psln-active');
    this.managepeopleService.getEmployees().subscribe(res => {
      this.volunteers = res;
      console.log(res);
    });
  }
  listVolunteers() {
    document.getElementById('emplo').classList.remove('psln-active');
    document.getElementById('fost').classList.remove('psln-active');
    document.getElementById('volun').classList.add('psln-active');
    this.managepeopleService.getVolunteers().subscribe(res => {
      this.volunteers = res;
      console.log(res);
    });
  }
  listFosters() {
    document.getElementById('emplo').classList.remove('psln-active');
    document.getElementById('fost').classList.add('psln-active');
    document.getElementById('volun').classList.remove('psln-active');
    this.volunteers = null;
  }
  deleteUser(user) {
    var r = confirm("Delete User? " + user.firstname);
    if (r == true) {
      this.managepeopleService.deleteUser(user.id).subscribe(res => {
        console.dir(res);
        this.toastr.success('User Delete!', 'Success!', { showCloseButton: true });
        if (user.UserTypeId == 1) {
          this.managepeopleService.getEmployees().subscribe(res => {
            this.volunteers = res;
            console.log(res);
          });
        }
        if (user.UserTypeId == 3) {
          this.managepeopleService.getVolunteers().subscribe(res => {
            this.volunteers = res;
            console.log(res);
          });
        }
      });
    } else {

    }
  }
}



