import { ModuleWithProviders }  from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

//Login & Register Components 
import { RegisterComponent } from './register/register.component';
import { LoginComponent } from './login/login.component';
//Home Components
import { DashboardComponent } from './home/dashboard/dashboard.component';
import { ViewprofileComponent } from './home/viewprofile/viewprofile.component';
import { PetsComponent } from './home/pet/pets/pets.component'; 
import { NewPetComponent } from './home/pet/new-pet/new-pet.component';
import { PetdetailComponent } from './home/pet/petdetail/petdetail.component';
import { AdoptionApplicationComponent } from './home/adoption-application/adoption-application.component';
import { FormsComponent } from './home/forms/forms.component';
import { WaitListComponent } from './home/wait-list/wait-list.component';
import { DoNotAdoptComponent } from './home/do-not-adopt/do-not-adopt.component';
import { ManagePeopleComponent } from './home/manage-people/manage-people.component';

//Contact Components
import { ContactComponent } from './contact/contact/contact.component';
import { MessagesComponent } from './contact/messages/messages.component';
import { EmailTemplateComponent } from './contact/template/email-template/email-template.component';
import { VolunteerOpportunitiesComponent } from './contact/volunteer-opportunities/volunteer-opportunities.component';
//Tool Components
import { ToolsComponent } from './tools/tools/tools.component';
import { RescueNetworkComponent } from './tools/rescue-network/rescue-network.component';
import { RegionalBreedTrackerComponent } from './tools/regional-breed-tracker/regional-breed-tracker.component';
import { TransportComponent } from './tools/transport/transport.component';
//User Component
import { UserComponent } from './user/user/user.component'; 
import { NewuserComponent } from './user/newuser/newuser.component';
import { UserdetailComponent } from './user/userdetail/userdetail.component';

import { NewTemplateComponent } from './contact/template/new-template/new-template.component';
import { NewQuestionComponent } from './contact/new-question/new-question.component';
import { QuestionTemplateComponent } from './contact/question-template/question-template.component';
import { ResumeMessageComponent } from './contact/resume-message/resume-message.component';
import { TrackerResultComponent } from './tools/tracker-result/tracker-result.component';
//Volunteer Components 
import { VolunteerPortalComponent } from './portal/volunteer-portal/volunteer-portal.component';
import { PortalOpportunitiesComponent } from './portal/portal-opportunities/portal-opportunities.component';
import { PortalHistoryComponent } from './portal/portal-history/portal-history.component';
import { PortalHoursComponent } from './portal/portal-hours/portal-hours.component';
import { PortalMyVolunteerComponent } from './portal/portal-my-volunteer/portal-my-volunteer.component';
import { PortalMyMessagesComponent } from './portal/portal-my-messages/portal-my-messages.component';
import { PortalMyHistoryComponent } from './portal/portal-my-history/portal-my-history.component';
import { PortalMyOrganizationsComponent } from './portal/portal-my-organizations/portal-my-organizations.component'; 




// Route Configuration
export const routes: Routes = [ 
   { path: '', component: LoginComponent },
   { path: 'register',component:RegisterComponent},
   { path: 'home', component: DashboardComponent },
   { path: 'profile', component: ViewprofileComponent },
   { path: 'contact', component: ContactComponent },
   { path: 'tools', component: ToolsComponent },
   { path: 'user', component: UserComponent },
   { path: 'newuser', component: NewuserComponent },
   { path: 'userdetail', component: UserdetailComponent },
   { path: 'pets', component: PetsComponent },
   { path: 'newPet', component: NewPetComponent },
   { path: 'petdetail', component: PetdetailComponent },
   { path: 'adoption', component: AdoptionApplicationComponent },
   { path: 'forms', component: FormsComponent },
   { path: 'waitList', component: WaitListComponent },
   { path: 'doNot', component: DoNotAdoptComponent },
   { path: 'managePeople', component: ManagePeopleComponent },
   { path: 'messages', component: MessagesComponent },
   { path: 'emailTemplate', component: EmailTemplateComponent },
   { path: 'volunteerOpportunities', component: VolunteerOpportunitiesComponent },
   { path: 'rescueNetwork', component: RescueNetworkComponent },
   { path: 'RegionalBreed', component: RegionalBreedTrackerComponent },
   { path: 'transport', component: TransportComponent },
   { path: 'newTemplate', component: NewTemplateComponent },
   { path: 'newQuestion', component: NewQuestionComponent },
   { path: 'QuestionTemplate', component: QuestionTemplateComponent },
   { path: 'ResumeMessage', component: ResumeMessageComponent },
   { path: 'trackerResult', component: TrackerResultComponent },
   { path: 'Portal/Volunteer', component: VolunteerPortalComponent },
   { path: 'Portal/Opportunities', component: PortalOpportunitiesComponent },
   { path: 'Portal/History', component: PortalHistoryComponent },
   { path: 'Portal/Hours', component: PortalHoursComponent },
   { path: 'Portal/Volunteers', component: PortalMyVolunteerComponent },
   { path: 'Portal/Messages', component: PortalMyMessagesComponent },
   { path: 'Portal/MyHistory', component: PortalMyHistoryComponent },
   { path: 'Portal/MyOrganizations', component: PortalMyOrganizationsComponent }   
];


export const routing: ModuleWithProviders = RouterModule.forRoot(routes);


