import { Component, OnInit, ViewContainerRef } from '@angular/core';
import {Observable} from 'rxjs/Observable'
import { UserService } from '../user/shared/user.service';
import { Router } from '@angular/router';
import { GetImageService } from '../../shared/socket/get-image.service';
import { ToastsManager } from 'ng2-toastr/ng2-toastr'; 

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css'],
  providers: [ GetImageService ]
})
export class UserComponent implements OnInit {
  employee:any={}; listUser:any;listAll:any;
  connection;
  msg:any;
  filter:string;

  constructor(
    private UserServices:UserService,
    private router:Router,
    private getImageService: GetImageService,
    public toastr: ToastsManager,
    vcr: ViewContainerRef) { 
      this.toastr.setRootViewContainerRef(vcr);
     }
  ngOnInit() {
    this.UserServices.getUsers().subscribe(res =>{
      this.listAll =res; 
      this.listUser  = this.listAll.filter(appuser => appuser.AppUser !=null);
      console.dir(res);
    });
  }
  getType(usertype){
    if(usertype=='Admins'){
      document.getElementById('admins').classList.add('psln-active');   
      document.getElementById('manager').classList.remove('psln-active');  
    }
    if(usertype=='Managers'){
      document.getElementById('admins').classList.remove('psln-active');   
      document.getElementById('manager').classList.add('psln-active');  
    }
    console.log(usertype);
    /* this.UserServices.getType(usertype).subscribe(res =>{ 
      this.listUser = res; 
      console.dir(res);
    });*/
  } 
  getImage(fileName) {
    if(fileName){
      return this.getImageService.getImage(fileName);
    }
    else{
      return '/assets/img/userimage.jpg';
    } 
  }
  viewDetail(pet){
    console.dir(pet);
    localStorage.setItem('user',JSON.stringify(pet));
    this.router.navigate(['/userdetail/']);
  }

  deleteUser(user){
    var r = confirm("Delete User? "+user.firstname);
    if (r == true) {
      this.UserServices.deleteUser(user.id).subscribe(res => {
        console.dir(res);
        this.toastr.success('User Delete!', 'Success!', { showCloseButton: true } );
        this.UserServices.getUsers().subscribe(res =>{
          this.listAll =res; 
          this.listUser  = this.listAll.filter(appuser => appuser.AppUser !=null);
          console.dir(res);
        });
      });
    }else{

    }
  }
}



