import { Injectable } from '@angular/core';
import {Observable} from 'rxjs/Observable'; 
import { SocketManagerService } from '../../../shared/socket/socket-manager.service';

@Injectable()
export class UserdetailService {

  constructor(private socketManager: SocketManagerService) { }

  updateEmployee(user){ 
    let observable = new Observable<any>(observer => {        
      this.socketManager.emit('update employee',{id:user.id,data:user}).then(res=>{
        observer.next(res);
          })
    });
    return observable;
  }
  updateVolunteer(user){ 
    let observable = new Observable<any>(observer => {        
      this.socketManager.emit('update volunteer',{id:user.id,data:user}).then(res=>{
        observer.next(res);
          })
    });
    return observable;
  }
  updateUser(user){ 
    let observable = new Observable<any>(observer => {        
      this.socketManager.emit('update user',{id:user.id,data:user}).then(res=>{
        observer.next(res);
          })
    });
    return observable;
  }
}



