import { Injectable } from '@angular/core';
import {Observable} from 'rxjs/Observable'; 
import { SocketManagerService } from '../../../shared/socket/socket-manager.service';

@Injectable()
export class ManagepeopleService {

  constructor(private socketManager: SocketManagerService) { }

  getVolunteers(){
    let observable = new Observable<any>(observer=>{
      this.socketManager.emit('get my org volunteers',{}).then(res=>{
        observer.next(res);
      })
    });
    return observable;
  }
  getEmployees(){
    let observable = new Observable<any>(observer=>{
      this.socketManager.emit('get my org employees',{}).then(res=>{
        observer.next(res);
      })
    });
    return observable;
  }
  deleteUser(id){ 
    console.log(id);
    let observable = new Observable<any>(observer => {        
      this.socketManager.emit('delete user',{id:id}).then(res=>{
        observer.next(res)
      })
    });
    return observable;
  }
}



