import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { NewuserService } from './shared/newuser.service';
import { FileUploadComponent } from '../../upload/file-upload/file-upload.component';
import { NewUploadService } from '../../upload/new-upload/service/new-upload.service';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { Router } from '@angular/router';

@Component({
  selector: 'app-newuser',
  templateUrl: './newuser.component.html',
  styleUrls: ['./newuser.component.css'],
  providers: [FileUploadComponent]
})
export class NewuserComponent implements OnInit {
  user: any = {}; msg: any;
  imgUp: any = {};
  url: String = 'https://upload.wikimedia.org/wikipedia/commons/7/7c/Profile_avatar_placeholder_large.png';
  constructor(
    private newuserService: NewuserService,
    private fileUploadComponent: FileUploadComponent,
    public toastr: ToastsManager,
    private router: Router,
    vcr: ViewContainerRef) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit() {
    this.imgUp.name = "";
    this.user.username = "";
    this.user.password = "";
    this.user.firstname = "";
    this.user.lastname = "";
    this.user.birthdate = "";
    this.user.email = "";
    this.user.phone = "";
    this.user.type = "";
  }
  validateData(user) {

    if (this.imgUp.name == "") {
      this.toastr.error('Required Image!', 'Oops!', { showCloseButton: true });
      return false;
    }
    if (this.user.username == "") {
      this.toastr.error('Required Username!', 'Oops!', { showCloseButton: true });
      document.getElementById("username").focus();
      return false;
    }
    if (this.user.firstname == "") {
      this.toastr.error('Required Firstname!', 'Oops!', { showCloseButton: true });
      document.getElementById("name").focus();
      return false;
    }
    if (this.user.lastname == "") {
      this.toastr.error('Required Lastname!', 'Oops!', { showCloseButton: true });
      document.getElementById("last").focus();
      return false;
    }
    if (this.user.birthdate == "") {
      this.toastr.error('Required Birth Date!', 'Oops!', { showCloseButton: true });
      document.getElementById("birth").focus();
      return false;
    }

    if (this.user.email == "") {
      this.toastr.error('Required Email!', 'Oops!', { showCloseButton: true });
      document.getElementById("email").focus();
      return false;
    }
    if (this.user.phone == "") {
      this.toastr.error('Required Phone!', 'Oops!', { showCloseButton: true });
      document.getElementById("phone").focus();
      return false;
    }
    if (this.user.type == "") {
      this.toastr.error('Required Type!', 'Oops!', { showCloseButton: true });
      document.getElementById("type").focus();
      return false;
    }

    return true;
  }
  createPJ(user) {
    if (this.validateData(this.user)) {
      this.toastr.info('Upload', 'Loading'); 
      if (user.type == "volunteer") {
        this.fileUploadComponent.saveFiles(this.imgUp).subscribe(result => {
          result.subscribe((res: any) => {
            const url = res[0].url;
            user.profileImage = url.substring(url.lastIndexOf('/') + 1, url.indexOf('?'));
            console.dir(user);
            this.newuserService.createVolunteer(user).subscribe(res => {
              console.dir(res);
              this.msg = res;
              if (this.msg.error) {
                this.toastr.error(this.msg.error, 'Oops!', { showCloseButton: true });
              } else {
                this.toastr.success('Volunteer Created!', 'Success!', { showCloseButton: true });
                this.router.navigate(['/user/']);
              }
            });
          });
        });
      }
      else if (user.type == "employees") {
        this.fileUploadComponent.saveFiles(this.imgUp).subscribe(result => {
          result.subscribe((res: any) => {
            const url = res[0].url;
            user.profileImage = url.substring(url.lastIndexOf('/') + 1, url.indexOf('?'));
            console.dir(user);
            this.newuserService.createEmployee(user).subscribe(res => {
              console.dir(res);
              this.msg = res;
              if (this.msg.error) {
                this.toastr.error(this.msg.error, 'Oops!', { showCloseButton: true });
              } else {
                this.toastr.success('Employee Created!', 'Success!', { showCloseButton: true });
                this.router.navigate(['/user/']);
              }
            });
          });
        });
      } else if (user.type == "user") {
        this.fileUploadComponent.saveFiles(this.imgUp).subscribe(result => {
          result.subscribe((res: any) => {
            const url = res[0].url;
            user.profileImage = url.substring(url.lastIndexOf('/') + 1, url.indexOf('?'));
            console.dir(user);
            this.newuserService.createUser(user).subscribe(res => {
              console.dir(res);
              this.msg = res;
              if (this.msg.error) {
                this.toastr.error(this.msg.error, 'Oops!', { showCloseButton: true });
              } else {
                this.toastr.success('User Created!', 'Success!', { showCloseButton: true });
                this.router.navigate(['/user/']);
              }
            });
          });
        });

      }
    }
  }

  readUrl(event: any) {
    if (event.target.files && event.target.files[0]) {
      const reader = new FileReader();
      reader.onload = (event: any) => {
        this.url = event.target.result;
        document.getElementById("preImage").style.backgroundImage = 'url(' + this.url + ')';
      }
      reader.readAsDataURL(event.target.files[0]);
    }
  }

  refreshImages(status) {
    this.imgUp = status;
  }


}



