import { Http } from '@angular/http';
import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs/Rx';
import { SocketManagerService } from '../../../shared/socket/socket-manager.service';
import { environment } from '../../../../environments/environment';

@Injectable()
export class NewUploadService {

  re = /(?:\.([^.]+))?$/;
  uploadProgress = new Subject<any>();
  uniqueID = function(){
       function chr4() {
         return Math.random().toString(16).slice(-4);
     }
     return chr4() + chr4() +
     '-' + chr4() +
     '-' + chr4() +
     '-' + chr4() +
     '-' + chr4() + chr4() + chr4();
 };

  getUniqueName= function(name){
   return this.uniqueID() + '.' + this.re.exec(name)[1];
  };


  constructor(private socketManager: SocketManagerService, private http: Http) { }

  requestCredentials = function (fileName) {
    // const observable = new Observable<any>(observer => {
    return this.http.get(environment.apiUrl + '/image/' + fileName);

  };

  updateUploadProgress(progress) {
    this.uploadProgress.next(progress);
  }
  getUploadPogress() {
    return this.uploadProgress;
  }

  getCredentials = function (files) {
    const credentialsObservables = [];

    if (files && files.length) {
      for (let x = 0; x <= files.length - 1; x++) {
        let uName = this.getUniqueName(files[x].name);
        credentialsObservables.push(this.requestCredentials(uName).map(res => {
          res = res.json();
          res.file = files[x];
          res.uName = uName;
          return res;
        }).catch(err => {
          console.log(err);
          Observable.of(err);
        }));
      }

      /*
      const uploads = [];
      Observable.forkJoin(credentialsObservables).subscribe(credentials => {
        credentials.map((credential: any) => {
          uploads.push(this.http.put(credential.url, credential.file, { headers: { 'Content-Type': credential.contentType } }));
        });
      });
      */

       return Observable.forkJoin(credentialsObservables);


  }
};
 


  }



