import { Component, OnInit, Input, Output, EventEmitter, HostListener } from '@angular/core';
import { NewUploadService } from '../new-upload/service/new-upload.service';
import {Observable} from 'rxjs/Rx';
import { Http, Headers } from '@angular/http';
import { ProgressHttp } from 'angular-progress-http';

@Component({
  selector: 'app-file-upload',
  templateUrl: './file-upload.component.html',
  styleUrls: ['./file-upload.component.css']
})
export class FileUploadComponent implements OnInit {
  errors: Array<string> =[];
  globalFiles: any;
  dragAreaClass: string = 'dragarea';
  textImage:string = 'textImage';
  display:string= 'block';
  @Input() projectId: number;
  @Input() sectionId: number;
  @Input() fileExt: string = "JPG, GIF, PNG";
  @Input() maxFiles: number = 5;
  @Input() maxSize: number = 5; // 5MB
  @Output() uploadStatus = new EventEmitter();
  
constructor( private newUploadService: NewUploadService,private http: Http, private httpProgress: ProgressHttp ) { }

ngOnInit() { }

onFileChange(event){
    let files = event.target.files; 
    this.globalFiles=files;
    this.uploadStatus.emit(files);
    this.textImage='textLoad';
    this.display='none';
}

@HostListener('dragover', ['$event']) onDragOver(event) {
    this.dragAreaClass = "droparea";
    event.preventDefault();
}

@HostListener('dragenter', ['$event']) onDragEnter(event) {
    this.dragAreaClass = "droparea";
    event.preventDefault();
}

@HostListener('dragend', ['$event']) onDragEnd(event) {
    this.dragAreaClass = "dragarea";
    event.preventDefault();
}

@HostListener('dragleave', ['$event']) onDragLeave(event) {
    this.dragAreaClass = "dragarea";
    event.preventDefault();
}
@HostListener('drop', ['$event']) onDrop(event) {   
    this.dragAreaClass = "dragarea";           
    event.preventDefault();
    event.stopPropagation();
    var files = event.dataTransfer.files;
    this.globalFiles=files;
    this.uploadStatus.emit(files);
    this.textImage='textLoad';
    this.display='none';
}

saveFiles(files) {
    this.errors = []; // Clear error
    // Validate file size and allowed extensions
    if (files.length > 0 && (!this.isValidFiles(files))) {
       // this.uploadStatus.emit(false);
        return;
    }
   /*
    if (files.length > 0) {
          let formData: FormData = new FormData();
          for (var j = 0; j < files.length; j++) {
              formData.append("file[]", files[j], files[j].name);
          }
          var parameters = {
              projectId: this.projectId,
              sectionId: this.sectionId
          }
          console.log("Carga Completa");
         // this.uploadStatus.emit(true);
      }
  */
  
      return this.newUploadService.getCredentials(files).map(credentials => {
          const uploads = [];
          for (let x = 0; x <= credentials.length - 1; x++) {
              const credential: any = credentials[x];
              const  headers = new Headers();
              headers.append('Content-Type', credential.contentType );
              const request = this.httpProgress
              .withUploadProgressListener(progress => { this.newUploadService.updateUploadProgress(progress); })
              .put(credential.url, credential.file, {headers});
              // const request = this.http.put(credential.url, credential.file, {headers});
             // request.subscribe()
              uploads.push(request);
          }
          return Observable.forkJoin(uploads);
      });
  
  }


private isValidFiles(files){
   // Check Number of files
    if (files.length > this.maxFiles) {
        this.errors.push("Error: At a time you can upload only " + this.maxFiles + " files");
        return;
    }        
    this.isValidFileExtension(files);
    return this.errors.length === 0;
}

private isValidFileExtension(files){
    // Make array of file extensions
      var extensions = (this.fileExt.split(','))
                      .map(function (x) { return x.toLocaleUpperCase().trim() });

      for (var i = 0; i < files.length; i++) {
          // Get file extension
          var ext = files[i].name.toUpperCase().split('.').pop() || files[i].name;
          // Check the extension exists
          var exists = extensions.includes(ext);
          if (!exists) {
              this.errors.push("Error (Extension): " + files[i].name);
          }
          // Check file size
          this.isValidFileSize(files[i]);
      }
}


private isValidFileSize(file) {
      var fileSizeinMB = file.size / (1024 * 1000);
      var size = Math.round(fileSizeinMB * 100) / 100; // convert upto 2 decimal place
      if (size > this.maxSize)
          this.errors.push("Error (File Size): " + file.name + ": exceed file size limit of " + this.maxSize + "MB ( " + size + "MB )");
}
}


