import { Injectable } from '@angular/core';

import { environment } from '../../../environments/environment';


@Injectable()
export class GetImageService {


  s3BasePath: String = environment.s3bucketURL;
  constructor() {

  }



  getImage(fileName) {
    return (this.s3BasePath + '/' + fileName);
  }



}



