import { Injectable } from '@angular/core';
import {Observable} from 'rxjs/Observable'; 
import { SocketManagerService } from '../../../../shared/socket/socket-manager.service';

@Injectable()
export class PetService {

  constructor(private socketManager: SocketManagerService) { 
    
  }
  getPets(){
    let observable = new Observable<any>(observer=>{
      this.socketManager.emit('get my org pets',{}).then(res=>{
        observer.next(res);
      })
    });
    return observable;   
  }
  getSpecies(){
    let observable = new Observable<any>(observer=>{
      this.socketManager.emit('all pet species',{}).then(res=>{
        observer.next(res);
      })
    });
    return observable;   
  }
  getBreeds(){
    let observable = new Observable<any>(observer=>{
      this.socketManager.emit('all pet breeds',{}).then(res=>{
        observer.next(res);
      })
    });
    return observable;   
  }
  getByStatus(status){
    console.log("Status Service",status);
    let observable = new Observable<any>(observer=>{
      this.socketManager.emit('get all pets by status',{status}).then(res=>{
        observer.next(res);
      })
    });
    return observable;   
  }
}



