import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { UserdetailService } from './shared/userdetail.service';
import { GetImageService } from '../../shared/socket/get-image.service';
import { FileUploadComponent } from '../../upload/file-upload/file-upload.component';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';


@Component({
  selector: 'app-userdetail',
  templateUrl: './userdetail.component.html',
  styleUrls: ['./userdetail.component.css'],
  providers: [GetImageService, FileUploadComponent]
})
export class UserdetailComponent implements OnInit {
  user: any = {};
  is_edit: boolean;
  btnEdit: string;
  msg: any;
  uuid: string;
  imgUp: any = {};
  url: String = 'https://upload.wikimedia.org/wikipedia/commons/7/7c/Profile_avatar_placeholder_large.png';

  constructor(
    private userdetailService: UserdetailService,
    private fileUploadComponent: FileUploadComponent,
    private getImageService: GetImageService,
    public toastr: ToastsManager,
    vcr: ViewContainerRef) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit() {
    this.imgUp.name = "";
    this.user.username = "";
    this.user.firstname = "";
    this.user.lastname = "";
    this.user.email = "";
    this.user.phone = "";
    this.user.type = ""; 
    this.user = JSON.parse(localStorage.getItem('user'));
    var date = this.user.birthdate;
    date=String(date).split('T');
    this.user.birthdate=date[0];
    this.user.password = "";
    if (this.user.profileImage) {
      this.url = this.getImage(this.user.profileImage);
    }
    else {
      this.url = '/assets/img/userimage.jpg';
    }
    document.getElementById("preImage").style.backgroundImage = 'url(' + this.url + ')';
    console.dir(this.user);
    this.uuid = this.user.id;
    this.is_edit = true;
    this.btnEdit = "Edit User";
    if (this.user.AppUserGroupId == 4) {
      this.user.type = 'volunteer';
    }
    if (this.user.AppUserGroupId == 3) {
      this.user.type = 'employees';
    }
    if (this.user.AppUserGroupId == 2) {
      this.user.type = 'user';
    }
  }
  validateData(user) {  
    if (this.user.username == "") {
      this.toastr.error('Required Username!', 'Oops!', { showCloseButton: true });
      document.getElementById("username").focus();
      return false;
    }
    if (this.user.firstname == "") {
      this.toastr.error('Required Firstname!', 'Oops!', { showCloseButton: true });
      document.getElementById("name").focus();
      return false;
    }
    if (this.user.lastname == "") {
      this.toastr.error('Required Lastname!', 'Oops!', { showCloseButton: true });
      document.getElementById("last").focus();
      return false;
    }
    if (this.user.birthdate == "") {
      this.toastr.error('Required Birth Date!', 'Oops!', { showCloseButton: true });
      document.getElementById("birth").focus();
      return false;
    }

    if (this.user.email == "") {
      this.toastr.error('Required Email!', 'Oops!', { showCloseButton: true });
      document.getElementById("email").focus();
      return false;
    }
    if (this.user.phone == "") {
      this.toastr.error('Required Phone!', 'Oops!', { showCloseButton: true });
      document.getElementById("phone").focus();
      return false;
    }
    if (this.user.type == "") {
      this.toastr.error('Required Type!', 'Oops!', { showCloseButton: true });
      document.getElementById("type").focus();
      return false;
    }

    return true;
  }
  updateUser(user) {
    if (this.btnEdit == "Edit User") {
      this.btnEdit = "Save Changes";
      this.is_edit = false;
    }
    else {
      user.id = this.uuid;
      if (this.validateData(this.user)) {
        this.toastr.info('Upload', 'Loading'); 
        if (user.type == "volunteer") {
          if (this.imgUp.name != "") {
            this.fileUploadComponent.saveFiles(this.imgUp).subscribe(result => {
              result.subscribe((res: any) => {
                const url = res[0].url;
                user.profileImage = url.substring(url.lastIndexOf('/') + 1, url.indexOf('?'));
                console.dir(user);
                this.userdetailService.updateVolunteer(user).subscribe(res => {
                  console.dir(res);
                  this.msg = res;
                  if (this.msg.error) {
                    this.toastr.error(this.msg.error, 'Oops!');
                  } else {
                    this.toastr.success('Volunteer Edit!', 'Success!');
                  }
                });
              });
            });
          } else {
            this.userdetailService.updateVolunteer(user).subscribe(res => {
              console.dir(res);
              this.msg = res;
              if (this.msg.error) {
                this.toastr.error(this.msg.error, 'Oops!');
              } else {
                this.toastr.success('Volunteer Edit!', 'Success!');
              }
            });
          }
        }
        else if (user.type == "employees") {
          if (this.imgUp.name != "") {
            this.fileUploadComponent.saveFiles(this.imgUp).subscribe(result => {
              result.subscribe((res: any) => {
                const url = res[0].url;
                user.profileImage = url.substring(url.lastIndexOf('/') + 1, url.indexOf('?'));
                console.dir(user);
                this.userdetailService.updateEmployee(user).subscribe(res => {
                  console.dir(res);
                  this.msg = res;
                  if (this.msg.error) {
                    this.toastr.error(this.msg.error, 'Oops!');
                  } else {
                    this.toastr.success('Employee Edit!', 'Success!');
                  }
                });
              });
            });
          }
          else {
            this.userdetailService.updateEmployee(user).subscribe(res => {
              console.dir(res);
              this.msg = res;
              if (this.msg.error) {
                this.toastr.error(this.msg.error, 'Oops!');
              } else {
                this.toastr.success('Employee Edit!', 'Success!');
              }
            });
          }
        } else if (user.type == "user") {
          if (this.imgUp.name != "") {
            this.fileUploadComponent.saveFiles(this.imgUp).subscribe(result => {
              result.subscribe((res: any) => {
                const url = res[0].url;
                user.profileImage = url.substring(url.lastIndexOf('/') + 1, url.indexOf('?'));
                console.dir(user);
                this.userdetailService.updateUser(user).subscribe(res => {
                  console.dir(res);
                  this.msg = res;
                  if (this.msg.error) {
                    this.toastr.error(this.msg.error, 'Oops!');
                  } else {
                    this.toastr.success('User Edit!', 'Success!');
                  }
                });
              });
            });
          } else {
            this.userdetailService.updateUser(user).subscribe(res => {
              console.dir(res);
              this.msg = res;
              if (this.msg.error) {
                this.toastr.error(this.msg.error, 'Oops!');
              } else {
                this.toastr.success('User Edit!', 'Success!');
              }
            });
          }
        }
      }
    }
  }
  getImage(fileName) {
    if (fileName) {
      return this.getImageService.getImage(fileName);
    }
    else {
      return '/assets/img/userimage.jpg';
    }
  }
  readUrl(event: any) {
    if (event.target.files && event.target.files[0]) {
      const reader = new FileReader();
      reader.onload = (event: any) => {
        this.url = event.target.result;
        document.getElementById("preImage").style.backgroundImage = 'url(' + this.url + ')';
      }
      reader.readAsDataURL(event.target.files[0]);
    }
  }

  refreshImages(status) {
    this.imgUp = status;
  }

}



