import { Component, OnInit } from '@angular/core';
import { PetService } from './shared/pet.service';
import { Router } from '@angular/router';
import { GetImageService } from '../../../shared/socket/get-image.service';
import { Pet } from './shared/pet';

@Component({
  selector: 'app-pets',
  templateUrl: './pets.component.html',
  styleUrls: ['./pets.component.css'],
  providers: [PetService, GetImageService]
})
export class PetsComponent implements OnInit {
  pets: Array<any>;
  filter: string;
  filterBar: Pet=new Pet();
  petBreed:Array<any>;
  petSpecies:Array<any>;
  constructor(private petService: PetService, private router: Router, private getImageService: GetImageService) { }

  ngOnInit() {
    this.petService.getPets().subscribe(res => {
      this.pets = res;
      console.dir(this.pets);
    });
    this.petService.getSpecies().subscribe(res => {
      this.petSpecies = res;
      console.dir(this.petSpecies);
    });
    this.petService.getBreeds().subscribe(res => {
      this.petBreed = res;
      console.dir(this.petBreed);
    });
  }
  getPets() {
    var clases = document.getElementsByClassName('psln-navUsersText2');
    for (var x = 0; x < clases.length; x++) {
      clases[x].classList.remove('psln-active');
    }
    document.getElementById('all').classList.add('psln-active');

    this.petService.getPets().subscribe(res => {
      this.pets = res;
      console.dir(this.pets);
    });
  }

  getImage(fileName) {
    if (fileName) {
      return this.getImageService.getImage(fileName);
    }
    else {
      return '/assets/img/pets.jpg';
    }
  }

  getByStatus(status) {
    var clases = document.getElementsByClassName('psln-navUsersText2');
    for (var x = 0; x < clases.length; x++) {
      clases[x].classList.remove('psln-active');
    }
    if (status == 'Adoptables') {
      document.getElementById('adoptable').classList.add('psln-active');
    }
    if (status == 'Medical Hold') {
      document.getElementById('medical').classList.add('psln-active');
    }
    if (status == 'Sanctuary') {
      document.getElementById('sanctuary').classList.add('psln-active');
    }
    if (status == 'Adopted') {
      document.getElementById('adopted').classList.add('psln-active');
    }
    if (status == 'Transferred') {
      document.getElementById('transferred').classList.add('psln-active');
    }
    if (status == 'Euthanized') {
      document.getElementById('euthanized').classList.add('psln-active');
    }
    console.log(status);
    this.petService.getByStatus(status).subscribe(res => {
      this.pets = res;
      console.dir(this.pets);
    });
  }
  viewDetail(pet) {
    console.dir(pet);
    localStorage.setItem('pet', JSON.stringify(pet));
    this.router.navigate(['/petdetail/']);
  }
}



